import Immutable from 'immutable';
import { DefaultDraftBlockRenderMap, DraftEditorCommand } from 'draft-js';

/**
 * Enum    ,      cite.
 */
export enum BlockType {
  /*  */
  h1 = 'header-one',
  h2 = 'header-two',
  h3 = 'header-three',
  h4 = 'header-four',
  h5 = 'header-five',
  h6 = 'header-six',
  /*  */
  blockquote = 'blockquote',
  /*    */
  code = 'code-block',
  /*  */
  list = 'unordered-list-item',
  /*   */
  orderList = 'ordered-list-item',
  /*  ( )*/
  cite = 'cite',
  /*   */
  default = 'unstyled',
}

/**
 * Enum    ,     ACCENT (       ).
 */
export enum InlineStyle {
  BOLD = 'BOLD',
  ITALIC = 'ITALIC',
  UNDERLINE = 'UNDERLINE',
  ACCENT = 'ACCENT' //    
}

/**
 * Enum    Entity.
 */
export enum EntityType {
  link = 'link',
}

/**
 * C  ()  
 * */
const CUSTOM_BLOCK_RENDER_MAP = Immutable.Map({
  [BlockType.cite]: {
    element: 'cite',
  },
});

//  ,     ,            DefaultDraftBlockRenderMap.merge
export const BLOCK_RENDER_MAP = DefaultDraftBlockRenderMap.merge(CUSTOM_BLOCK_RENDER_MAP);

/**
 * C  ()   
 * */
export const CUSTOM_STYLE_MAP = {
  [InlineStyle.ACCENT]: {
    backgroundColor: 'gray',
    color: 'white',
  },
};

export type KeyCommand = DraftEditorCommand | 'accent'; //  